.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.button {
  background-color: lightblue;
  border-radius: 12px;
  width: 140px;
  height: 40px;
  margin-left: auto;
  margin-right: 5%;
}

.header-website {
  background-color: lightgrey;
  display: flex;
  align-items: center
}

.map-naver-view {
  min-height: 500px;
  width: 90%;
  margin: 0 auto;
}

.title-website {
  margin-left: 35px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
